import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useParams } from "react-router-dom";
import TableItem from "../components/TableItem";
import { get_singer, get_songs } from "../redux/actions/main";

function Singer() {
    const { slug, id } = useParams();
    const [singer, setSinger] = useState(null);
    const [songs, setSongs] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const dispatch = useDispatch();
    const getSinger = () => {
        setIsLoading(true);
        dispatch(get_singer(slug, id)).then((result) => {
            setSinger(result);
            setIsLoading(false);
        });
    };
    useEffect(() => {
        window.scrollTo({
            top: 0,
            behavior: "smooth",
        });
        getSinger();
    }, [slug, id]);
    useEffect(() => {
        if (singer) {
            setIsLoading(true);
            const filt = `singers=${singer?.id}&ordering=-created&limit=50`;
            dispatch(get_songs(filt)).then((res) => {
                setSongs(res.results);
                setIsLoading(false);
            });
        }
    }, [singer]);
    return (
        <div className="container">
            <div className="h1 my-5 text-center truncate-overflow-2">
                {isLoading ? (
                    <div class="spinner-border h6" role="status">
                        <span class="visually-hidden">Loading...</span>
                    </div>
                ) : (
                    singer && singer.name
                )}
            </div>
            <div className="container">
                <div
                    className="row my-3 pb-3 align-items-center"
                    style={{
                        color: "#b3b3b3",
                        fontSize: "13px",
                        letterSpacing: "1px",
                        fontWeight: 600,
                        borderBottom: "1px solid rgba(255,255,255,.1)",
                    }}
                >
                    <div className="col-1 fs-6 d-flex justify-content-center">
                        #
                    </div>
                    <div className="col">НАЗВАНИЕ</div>
                    <div className="col-3 d-none d-md-flex justify-content-center">
                        ДАТА ДОБАВЛЕНИЯ
                    </div>
                    <div className="col-2 d-flex justify-content-center">
                        <i className="bi bi-clock fs-6"></i>
                    </div>
                </div>
                {Array.isArray(songs) &&
                    songs.length >= 1 &&
                    songs.map((song, index) => {
                        return (
                            <TableItem
                                key={song?.id}
                                id={song?.id}
                                name={song?.name}
                                duration={song?.duration}
                                song_file={song.song_file}
                                date={song.created}
                                authors={song.singers}
                            />
                        );
                    })}
            </div>
        </div>
    );
}

export default Singer;
