import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { selectMusic } from "../redux/selectors/music";
import { SET_MUSIC } from "../redux/types/types";

function TableItem(props) {
    const music = useSelector((state) => selectMusic(state));
    const { id, authors, name, duration, date } = props;
    const [play, setPlay] = useState(false);
    const [currentMusic, setCurrentMusic] = useState(false);
    const getReleaseTime = () => {
        let today = new Date();
        let release = new Date(date);
        const diffTime = Math.abs(today - release);
        const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
        return diffDays;
    };
    const getDurationTime = () => {
        var sec_num = parseInt(duration, 10); // don't forget the second param
        var hours = Math.floor(sec_num / 3600);
        var minutes = Math.floor((sec_num - hours * 3600) / 60);
        var seconds = sec_num - hours * 3600 - minutes * 60;

        if (minutes < 10) {
            minutes = "0" + minutes;
        }
        if (seconds < 10) {
            seconds = "0" + seconds;
        }
        return minutes + ":" + seconds;
    };

    const dispatch = useDispatch();
    useEffect(() => {
        if (music?.id === id) {
            setCurrentMusic(true);
        } else {
            setCurrentMusic(false);
        }
    }, [music]);
    return (
        <div
            className="row pb-2 mb-2 pt-2 align-items-center btn-nav"
            style={{
                color: "#b3b3b3",
                fontSize: "14px",
            }}
        >
            <div
                onMouseEnter={() => setPlay(true)}
                onMouseLeave={() => setPlay(false)}
                className="col-1 ps-3 fs-6 d-flex justify-content-center play-btn"
                role="button"
            >
                {currentMusic ? (
                    <i
                        role="button"
                        // onClick={pause}
                        class="bi bi-pause-circle-fill fs-5"
                    ></i>
                ) : play ? (
                    <i
                        class="bi bi-play-circle-fill"
                        onClick={() => {
                            dispatch({ type: SET_MUSIC, payload: props });
                        }}
                    ></i>
                ) : (
                    <i class="bi bi-play-fill"></i>
                )}
            </div>
            <div className="col d-flex justify-content-start align-items-center ">
                <img
                    src="https://i.scdn.co/image/ab67616d00004851c5909eb7b91b1e9a0a3a74bf"
                    style={{
                        width: "40px",
                        borderRadius: "3px",
                        height: "40px",
                    }}
                />
                <div className="d-flex ms-3 flex-column justify-content-between ">
                    <div className="fs-6 fw-bold text-white truncate-overflow-1">
                        {name}
                    </div>
                    {Array.isArray(authors) && authors.length >= 1 && (
                        <div className="d-flex">
                            {authors.map((aut, index) => {
                                return (
                                    <>
                                        <Link
                                            to={`/singer/${aut.slug}/${aut.id}`}
                                            style={{
                                                textTransform: "capitalize",
                                            }}
                                            className="gray-text text-decoration-none d-inline fw-normal truncate-overflow-1"
                                        >
                                            {aut?.name}
                                        </Link>
                                        {authors.length > index + 1 && (
                                            <span className="px-1">,</span>
                                        )}
                                    </>
                                );
                            })}
                        </div>
                    )}
                </div>
            </div>
            <div className="col-3 d-none d-md-flex justify-content-center">
                {getReleaseTime()} days ago
            </div>
            <div className="col-2 d-flex justify-content-center">
                {getDurationTime()}
            </div>
        </div>
    );
}

export default TableItem;

